  connect_metamask = async () => {
    // get window.ethereum and set it to state
    this.setState({
      ethereum: window.ethereum
    });
    // connect metamask to the dapp
    await window.ethereum.enable();
    // get the provider and set it to state
    this.setState({
      provider: new ethers.providers.Web3Provider(window.ethereum)
    });
    // set to conencted
    this.setState({
      connected: true
    });
    // get the account and set it to state\
    this.setState({
      account: await this.state.provider.getSigner().getAddress()
    });
    // make sure its bsc network
    // console log all states
    //console.log(this.state);
    let chainId = await this.state.provider.getNetwork();
    chainId = await chainId.chainId;
    if (chainId !== 56) {
      alert("Please connect to the bsc network", chainId);
      console.log("Please connect to the bsc network", chainId);
      this.setState({
        connected: false
      });
    }
    // load       raffles: [],
    this.loadRaffles();
  }

  save_raffle_id = async (raffle_id) => {
    // save the raffles we bought to local storage
    let raffles = await localStorage.getItem("raffles");
    if (raffles === null) {
      raffles = [];
    } else {
      raffles = JSON.parse(raffles);
    }
    raffles.push(raffle_id);
    await localStorage.setItem("raffles", JSON.stringify(raffles));
  }

  is_remvoed = (raffle_id) => {
    // check if the raffle is removed
    let raffles = localStorage.getItem("raffles_removed");
    if (raffles === null) {
      raffles = [];
      return true;
    } else {
    raffles = JSON.parse(raffles);
    if (raffles.includes(raffle_id.toString())) {
      return false;
    }
    return true;
    }
  }
  remove_raffle_id = async (raffle_id) => {
    // remove the raffles if user wants to
    let raffles = localStorage.getItem("raffles_removed");
    raffles = JSON.parse(raffles);
    if (raffles === null) {
      raffles = [];
    }
    raffles.push(raffle_id.toString());
    await localStorage.setItem("raffles_removed", JSON.stringify(raffles));
    // reload the raffles
    this.loadRaffles();

  }
  
