  create_raffle = async (ticket_price, raffle_end_balance) => {
    // get the contract
    const contract = new ethers.Contract(this.state.contract, this.state.abi, this.state.provider.getSigner());
    // call the function
    const tx = await contract.createRaffle(ticket_price, raffle_end_balance);
    // wait for the transaction to be mined
    await tx.wait();
    // console log the transaction
    console.log(tx);
  }
  // create raffle erc20
