  // buy ticket
  buy_ticket = async (raffle_id, ticket_price) => {
    // metamask pop up to send the transaction
    // get the contract
    alert("You can only buy one ticket per raffle, if you buy more than one ticket your transaction will be reverted");
    // create the tx
    var price = ethers.utils.parseEther(ticket_price.substring(2))["_hex"];
    // to wei
    // parshe to wei
    var price_wei = ethers.utils.parseUnits(ticket_price.substring(2), "wei")["_hex"];
    console.log(price);
    const tx = {
      from: this.state.account,
      to: this.state.contract,
      value: price_wei.toString(),
      data: new ethers.utils.Interface(this.state.abi).functions.buyTicket.encode([raffle_id]),
      // 1 dollar cent gass fee, dont do callulation
      gasLimit: ethers.utils.hexlify(100000),
      gasPrice: ethers.utils.hexlify(1000000000)



    };
    // ask metamask to send the transaction
    const send = await this.state.ethereum.send('eth_sendTransaction', [tx]);
    this.save_raffle_id(raffle_id);
    // reload the raffles
    this.loadRaffles();


  }
  // buy ticket erc20
  buy_ticket_erc20 = async (raffle_id, ticket_price) => {
    // metamask pop up to send the transaction
    // get the contract
    //alert("You can only buy one ticket per raffle, if you buy more than one ticket your transaction will be reverted");
    // use yes no popup
    let bool = window.confirm("You can only buy one ticket per raffle, if you buy more than one ticket your transaction will be reverted");
    if (!bool) {
      return;
    }
    const tx = {
      from: this.state.account,
      to: this.state.contract,
      data: new ethers.utils.Interface(this.state.abi).functions.buyTicketWithERC20.encode([raffle_id]),
      gasPrice: ethers.utils.hexlify(5000000000),
      gasLimit: ethers.utils.hexlify(482928)
    };
    // now tx to approve the contract to spend your tokens
    const tx2 = {
      from: this.state.account,
      to: this.state.erc20,
      data: new ethers.utils.Interface(this.state.erc20_abi).functions.approve.encode([this.state.contract, ticket_price])
    };
    // ask metamask to send the transaction
    const send1 = await this.state.ethereum.send('eth_sendTransaction', [tx2]);
    const send = await this.state.ethereum.send('eth_sendTransaction', [tx]);
    this.save_raffle_id(raffle_id);
    // reload the raffles
    this.loadRaffles();
  }
